(function () {

  function AuthCtrl ($scope, $rootScope, $location, AuthService) {

    const auth = this;

    AuthService.ClearCredentials();

    auth.login = function () {
      auth.dataLoading = true;
      AuthService.login(auth.username, auth.password).save({}, (response) => {
        AuthService.SetCredentials(auth.username, auth.password, true);
        $rootScope.$broadcast('logged-in');
        $location.path('/home');
      }, (err) => {
        $rootScope.$broadcast('logged-out');
        auth.error = err.data;
        auth.dataLoading = false;
      });
    };
  }


  angular.module('authControllers', []).
    controller('AuthCtrl', AuthCtrl);

  /** Inject the dependencies - avoid problems during minification **/
  AuthCtrl.$inject = ['$scope', '$rootScope', '$location', 'AuthService'];
}());
